*{
    padding: 0;
    margin: 0;
    border: 0;
}

button {
    cursor: pointer;
}

a {
  color: inherit;
  text-decoration: none;
  margin: 0;
}

a, a:visited {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
}

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}



